import axios from 'axios';

export function handleSignup(alertComponent) {
    //      
    if (this.signupData.password !== this.signupData.confirmPassword) {
        alertComponent.showAlert('  .');
        return;
    }

    //    (:    )
    console.log('  ...', this.signupData);

    // signupData URL   
    const queryParams = new URLSearchParams(this.signupData).toString();

    axios.get(`https://jiyoung.pythonanywhere.com/api/signup/?${queryParams}`, {
        headers: {
            'Content-Type': 'application/json'
        },
        withCredentials: true
    })
    .then(response => {
        console.log(' :', response.data);
        const routeQueryParams = this.$route.query;
        alertComponent.showAlert(' .', () => {
            //    CardRecom  
            this.$router.push({
                name: 'CardRecom',
                query: routeQueryParams
            });
        });
    })
    .catch(error => {
        console.error(' :', error);
        const routeQueryParams = this.$route.query;
        alertComponent.showAlert('  .', () => {
            //     CardRecom  
            this.$router.push({
                name: 'CardRecom',
                query: routeQueryParams
            });
        });
    });
}

export function redirectToLogin() {
    //   
    console.log('  ');
    //      
    this.$router.push('/login');
}
