export function handleLogin(alertComponent) {
    //    (:    )
    console.log('  ...', this.loginData);
    // , ngrok     
    fetch('https://jiyoung.pythonanywhere.com/api/login/', {
    method: 'POST',
    headers: {
    'Content-Type': 'application/json'
    },
    body: JSON.stringify(this.loginData)
})
    .then(response => response.json())
    .then(data => {
    if (data.success) {
        console.log(' :', data);
        alertComponent.showAlert(' .', 'https://young-jii.github.io/saveus/card-recom');
        //     (:  )
    } else {
        console.error(' :', data.message);
        alertComponent.showAlert(' :', 'https://young-jii.github.io/saveus/card-recom');
    }
    })
    .catch(error => {
    console.error(' :', error);
    alertComponent.showAlert(' .', 'https://young-jii.github.io/saveus/card-recom'); //     
    });
}

export function redirectToLogin() {
//   
console.log('  ');
//      
this.$router.push('/login');
}

export function redirectToSignup(queryParams) {
    console.log('  ');
    this.$router.push({
        path: '/signup',
        query: queryParams
    });
}